import React, {Component} from 'react';
import logo from './logo.svg';
import {Navbar, Nav, NavItem} from 'react-bootstrap'
import './App.css';

const README = require('../../README.md')

const exampleName = document.location.pathname.replace('/', '')
let ExampleComponent

class App extends Component {

  constructor(props) {
    super(props)
    this.state = {
      showExample: undefined
    }
  }

  componentDidMount() {
    if (exampleName) {
      import(`./${exampleName}`).then((r) => {
        ExampleComponent = r.default
        this.setState({showExample: true})
      })
    } else {
      this.setState({showExample: false})
    }
  }

  render() {
    const {showExample} = this.state
    const showReadme = (showExample === false)

    return (
      <div className="App">
        <header className="App-header" style={{height: 'auto'}}>
          <a href="/"><img src={logo} className="App-logo" alt="logo"/></a>
          <h1 className="App-title">Welcome to Redux Tide examples</h1>


          <a
            className="github-button"
            href="https://github.com/edtoken/redux-tide"
            data-icon="octicon-star"
            data-size="large"
            data-show-count="true"
            aria-label="Star edtoken/redux-tide on GitHub">Star</a>

          <br/>

          <a
            className="github-button"
            href="https://github.com/edtoken"
            data-size="large"
            data-show-count="true"
            aria-label="Follow @edtoken on GitHub">Follow @edtoken</a>

        </header>
        <div className="App-intro">
          <div>
            <Navbar>
              <Nav>
                <NavItem href="/">
                  Index
                </NavItem>
                <NavItem href="/blog">
                  Blog example
                </NavItem>
                <NavItem href="/counter">
                  Counter example (soon)
                </NavItem>
                <NavItem href="/todos">
                  Todos example (soon)
                </NavItem>
              </Nav>
            </Navbar>
          </div>
        </div>
        {showReadme && <div>
          <div style={{
            textAlign: 'left',
            maxWidth: '800px',
            margin: '30px auto',
            padding: '30px',
            border: '1px solid #ddd'
          }} dangerouslySetInnerHTML={{__html: README}}/>
        </div>}

        {showExample && ExampleComponent &&
        <div style={{
          textAlign: 'left',
          maxWidth: '800px',
          margin: '30px auto',
          padding: '30px',
          border: '1px solid #ddd'
        }}>
          <ExampleComponent/>
        </div>}
      </div>
    );
  }
}

export default App;



